* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f0f0f0;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  box-shadow: 0 2px 10px #0000001a;
}

.pricing-box {
  max-height: fit;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 10px;
  padding: 20px;
  transition: max-height .3s ease-out;
  position: relative;
  overflow: hidden;
}

.price-header {
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  display: flex;
}

.discount {
  color: red;
  background-color: #fcc;
  border-radius: 5px;
  padding: 5px 10px;
  font-weight: bold;
}

.price {
  font-weight: bold;
}

.most-popular {
  color: #d7a505;
  background-color: #ffe599;
  border-radius: 5px;
  padding: 5px;
  font-size: 12px;
}

.hidden {
  display: none;
}

.expanded .box-content {
  margin-top: 10px;
  animation: .3s ease-out expand;
  display: block;
}

@keyframes expand {
  from {
    max-height: 0;
  }

  to {
    max-height: 1000px;
  }
}

.add-to-cart {
  color: #fff;
  cursor: pointer;
  background-color: #ff4d4d;
  border: none;
  border-radius: 8px;
  width: 100%;
  margin-top: 20px;
  padding: 15px;
  font-size: 16px;
}

.add-to-cart:hover {
  background-color: #e63939;
}

.options {
  margin: auto;
  display: flex;
}
